import React, { useState } from 'react';
import { v4 as uuidv4 } from 'uuid';
import * as iscp from '@aptpod/iscp-ts'

const sleepMs = (ms: number) => new Promise<void>((resolve) => setTimeout(resolve, ms))
const getNowTimeNano = () => BigInt(Date.now()) * BigInt(1000_000)

const App: React.FC = () => {
    const [urlString, setUrlString] = useState('https://intdash-cluster-dev.intdash.jp');
    const [token, setToken] = useState('JKQu4HiTcQmAvu279QXusMwwzejGp5nFpIMIhXhiq8Gh9sSA.4kXTu7b81f6HihHBMS1cShDLHy4fjvEm3vuDJ4IoAEU');
    const [nodeId, setNodeId] = useState('29866475-8a4d-45cb-9be7-5abac4382c6c');
    const [dataName, setDataName] = useState('data_name');
    const [dataType, setDataType] = useState('bytes');
    const [payloadSize, setPayloadSize] = useState('64');
    const [count, setCount] = useState('10');
    const [interval, setInterval] = useState('1000');
    const [result, setResult] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    const fetchData = async () => {
        setIsLoading(true);
        try {
            const tokenSource = async () => {
              const response = await fetch(`${urlString}/api/iscp/tickets`, {
                  method: 'POST',
                  headers: {'X-Intdash-Token': token},
              });
              if (!response.ok) {
                  throw new Error('Network response was not ok');
              }
              const data = await response.json();
              return data["ticket"];
            }

            const url = new URL(urlString);
            const enableTLS = url.protocol=="https:";
            const connector = new iscp.WebSocketConnector({enableTLS: enableTLS});

            const conn = await iscp.Conn.connect({
              address: url.hostname,
              connector,
              tokenSource,
              nodeId: nodeId,
            });

            const sessionId = uuidv4();
            const upstream = await conn.openUpstream({
              sessionId: sessionId,
              qos: iscp.QOS.UNRELIABLE,
              flushPolicy: iscp.FlushPolicy.immediately(),
              persist: false,
            });
            const downstream = await conn.openDownstream({
              filters: [iscp.DownstreamFilter.allFor(nodeId)],
            });

            downstream.addEventListener(iscp.Downstream.EVENT.METADATA, (metadata) => {
              console.log(metadata);
              // TODO
            });
            downstream.addEventListener(iscp.Downstream.EVENT.CHUNK, (chunk) => {
              console.log(chunk);
              // TODO
            });

            const start = getNowTimeNano();
            await sleepMs(1000);
            await conn.sendBaseTime(
              new iscp.BaseTime({
                name: 'manual',
                elapsedTime: getNowTimeNano() - start,
                baseTime: start,
                priority: 1000,
                sessionId: sessionId,
              }),
            );

            for (let i = 0; i < Number(count); i++) {
              await sleepMs(Number(interval))
              await upstream.writeDataPoints(new iscp.DataId({name: dataName, type: dataType}), [
                new iscp.DataPoint({
                  elapsedTime: getNowTimeNano() - start,
                  payload: new TextEncoder().encode(`hello: ${i}`),
                }),
              ]);
              setResult(`${i}`);
            }

        } catch (error) {
            console.log(error);
            setResult('通信に失敗しました。');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div>
            <input type="text" value={urlString} onChange={e => setUrlString(e.target.value)} placeholder="URL" /><br/>
            <input type="text" value={token} onChange={e => setToken(e.target.value)} placeholder="API Token" /><br/>
            <input type="text" value={nodeId} onChange={e => setNodeId(e.target.value)} placeholder="Data Name" /><br/>
            <input type="text" value={dataName} onChange={e => setDataName(e.target.value)} placeholder="Data Name" /><br/>
            <input type="text" value={dataType} onChange={e => setDataType(e.target.value)} placeholder="Data Type" /><br/>
            <input type="text" value={payloadSize} onChange={e => setPayloadSize(e.target.value)} placeholder="Payload Size" /><br/>
            <input type="text" value={count} onChange={e => setCount(e.target.value)} placeholder="Count" /><br/>
            <input type="text" value={interval} onChange={e => setInterval(e.target.value)} placeholder="Interval" /><br/>
            <button onClick={fetchData} disabled={isLoading}>送信</button>
            {isLoading ? <div>ローディング中...</div> : <div><p>結果: {result}</p></div>}
        </div>
    );
};

export default App;
